import React, { useState, useRef, useEffect } from 'react';
import Cropper from 'react-easy-crop';

import {
  ref,
  uploadBytesResumable,
  getDownloadURL,
  deleteObject
} from 'firebase/storage';
import { doc, onSnapshot, updateDoc } from 'firebase/firestore';
import { storage, firestore } from '../firebase';

function ImageCropper({
  closeModal,
  photoURL,
  handleImageUpload,
  setPhotoURL,
  user,
  collectionName,
}) {
  const [image, setImage] = useState(photoURL);
  const [selectedFileName, setSelectedFileName] = useState(null);
  const [docRef, setDocRef] = useState(null);
  const fileInputRef = useRef(null);
  const [cropperRef, setCropperRef] = useState(null);
  const [isUploadSuccess, setIsUploadSuccess] = useState(false);
  const [crop, setCrop] = useState({ x: 0, y: 0 });
  const [zoom, setZoom] = useState(1);
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null);
  const [showFlash, setShowFlash] = useState(false); // State for flash effect
  const defaultImageURL = `${process.env.PUBLIC_URL}/images/arts/user.webp`;

  useEffect(() => {
    document.body.classList.add('modal-open');

    return () => {
      document.body.classList.remove('modal-open');
    };
  }, []);

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      setSelectedFileName(file.name);
      const reader = new FileReader();
      reader.onload = (event) => {
        setImage(event.target.result);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleUpload = async () => {
    if (!croppedAreaPixels) {
      console.error('Crop area not selected');
      return;
    }
  
    try {
      const blob = await fetch(image).then((res) => res.blob());
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      const imageToCrop = new Image();
  
      imageToCrop.onload = async () => {
        canvas.width = croppedAreaPixels.width;
        canvas.height = croppedAreaPixels.height;
  
        ctx.drawImage(
          imageToCrop,
          croppedAreaPixels.x,
          croppedAreaPixels.y,
          croppedAreaPixels.width,
          croppedAreaPixels.height,
          0,
          0,
          croppedAreaPixels.width,
          croppedAreaPixels.height
        );
  
        canvas.toBlob(async (blob) => {
        try {
          const storageRef = ref(storage, `profileImages/${selectedFileName}`);
          const metadata = { contentType: 'image/jpeg' };
  
          // Delete old image
          if (photoURL && photoURL !== defaultImageURL) {
            const oldImageRef = ref(storage, photoURL);
            await deleteObject(oldImageRef);
          }
  
          // Upload new image
          const uploadTask = uploadBytesResumable(storageRef, blob, metadata);
  
          uploadTask.on(
            'state_changed',
            (snapshot) => {
              // Handle upload progress here
            },
            (error) => {
              console.error('Upload error:', error);
            },
            async () => {
              const downloadURL = await getDownloadURL(uploadTask.snapshot.ref);
              if (downloadURL) {
                // Update the Firestore document immediately before handling the upload success
                await updateDoc(docRef, { photoURL: downloadURL });
                handleImageUpload(downloadURL);
                setIsUploadSuccess(true);
  
                // Trigger flash effect
                setShowFlash(true);
                setTimeout(() => {
                  setShowFlash(false);
                }, 500); // Adjust the duration of the flash
  
                setTimeout(() => {
                  setIsUploadSuccess(false);
                }, 3000);
  
                // Set the uploaded image as the new image in the Cropper
                setImage(downloadURL); // Set the uploaded image as the new image
                setCroppedAreaPixels(null); // Reset cropped area
                setZoom(1); // Reset zoom level
                setCrop({ x: 0, y: 0 }); // Reset crop position
              }
            }
          );
        } catch (error) {
          console.error('Error uploading image:', error);
        }
      }, 'image/jpeg');
    };

    imageToCrop.src = URL.createObjectURL(blob);
  } catch (error) {
    console.error('Error handling upload:', error);
  }
};
  

  const handleDeletePhoto = async () => {
    if (user && user.uid) {
      const defaultImageURL = `${process.env.PUBLIC_URL}/images/arts/user.webp`;
      await updateDoc(docRef, { photoURL: defaultImageURL });
      setPhotoURL(defaultImageURL);
    }
  };

  useEffect(() => {
    fileInputRef.current = document.createElement('input');
    fileInputRef.current.type = 'file';
    fileInputRef.current.accept = 'image/*';
    fileInputRef.current.addEventListener('change', handleFileUpload);

    const userDocRef = doc(firestore, collectionName, user.uid);
    setDocRef(userDocRef);

    const handlePhotoURLChange = (newPhotoURL) => {
      setImage(newPhotoURL);
    };

    const unsubscribe = onSnapshot(userDocRef, (doc) => {
      const newPhotoURL = doc.data()?.photoURL;
      handlePhotoURLChange(newPhotoURL);
    });

    return () => unsubscribe();
  }, [user.uid, collectionName]);

  const onCropComplete = (croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  };

  return (
    <div className="modal-overlay">
      <div className="modalcrop">
        <div className="top-section">
          <div style={{ marginRight: '10px' }}>Profile image</div>
          <button className="btn-crop" onClick={closeModal}>
            <svg
              xmlns="http://www.w3.org/2000/svg"
              fill="none"
              viewBox="0 0 24 24"
              stroke="white"
              strokeWidth="1.5"
              className={`w-6 h-6 smaller-svg`}
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        </div>
        <div className={showFlash ? 'flash' : ''}></div> {/* Flash effect */}
        <div className='centerimage'>

          {image && (
            <Cropper
              ref={setCropperRef}
              image={image}
              crop={crop}
              zoom={zoom}
              onCropChange={setCrop}
              onZoomChange={setZoom}
              onCropComplete={onCropComplete}
              cropShape="round"
              aspect={4 / 4}
            />

          )}

        </div>
        <div className="button-section">
          <button
            className="btn-crop"
            onClick={() => fileInputRef.current.click()}
          >
            <div className="button-content">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="white"
                strokeWidth="1.5"
                className={`w-6 h-6 smaller-svg`}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M6.827 6.175A2.31 2.31 0 015.186 7.23c-.38.054-.757.112-1.134.175C2.999 7.58 2.25 8.507 2.25 9.574V18a2.25 2.25 0 002.25 2.25h15A2.25 2.25 0 0021.75 18V9.574c0-1.067-.75-1.994-1.802-2.169a47.865 47.865 0 00-1.134-.175 2.31 2.31 0 01-1.64-1.055l-.822-1.316a2.192 2.192 0 00-1.736-1.039 48.774 48.774 0 00-5.232 0 2.192 2.192 0 00-1.736 1.039l-.821 1.316z"
                />
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M16.5 12.75a4.5 4.5 0 11-9 0 4.5 4.5 0 019 0zM18.75 10.5h.008v.008h-.008V10.5z"
                />
              </svg>
              New Photo
            </div>
          </button>
          <div className="success">
            <div className="button-container">
              <button className="btn-crop" onClick={handleUpload}>
                <div className="button-content">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    fill="none"
                    viewBox="0 0 24 24"
                    stroke="white"
                    strokeWidth="1.5"
                    className={`w-6 h-6 smaller-svg`}
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      d="M3 16.5v2.25A2.25 2.25 0 0 0 5.25 21h13.5A2.25 2.25 0 0 0 21 18.75V16.5m-13.5-9L12 3m0 0 4.5 4.5M12 3v13.5"
                    />
                  </svg>
                  Upload Photo
                </div>
              </button>
            </div>
            {isUploadSuccess && (
              <div className="upload-success-icon">
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  color="#009E60"
                  fill="none"
                  viewBox="0 0 24 24"
                  strokeWidth={2}
                  stroke="currentColor"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M4.5 12.75l6 6 9-13.5"
                  />
                </svg>
              </div>
            )}

          </div>

          <button className="btn-crop" onClick={handleDeletePhoto}>
            <div className="button-content">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                stroke="white"
                strokeWidth="1.5"
                className={`w-6 h-6 smaller-svg`}
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  d="M14.74 9l-.346 9m-4.788 0L9.26 9m9.968-3.21c.342.052.682.107 1.022.166m-1.022-.165L18.16 19.673a2.25 2.25 0 01-2.244 2.077H8.084a2.25 2.25 0 01-2.244-2.077L4.772 5.79m14.456 0a48.108 48.108 0 00-3.478-.397m-12 .562c.34-.059.68-.114 1.022-.165m0 0a48.11 48.11 0 013.478-.397m7.5 0v-.916c0-1.18-.91-2.164-2.09-2.201a51.964 51.964 0 00-3.32 0c-1.18.037-2.09 1.022-2.09 2.201v.916m7.5 0a48.667 48.667 0 00-7.5 0"
                />
              </svg>
              Delete Photo
            </div>
          </button>
        </div>
      </div>
    </div>
  );
}

export default ImageCropper;